:root {
  --dark-color: #3F4F6B;
  --light-color: #cfe0ff;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  padding: 40px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1px;
  background-color: var(--light-color);
  color: var(--dark-color);
}

.wrapper {
  max-width: 270px;
  padding: 5px;
  box-shadow: 0px 0px 10px 1px var(--dark-color);;
  border-radius: 5px;
}

.info {
  min-height: 80px;
  display: flex;
  flex-direction: column;
}

.info > * {
  margin: auto;
}

.increased {
  font-size: 36px;
  font-weight: 700;
}

.colored {
  color: tomato;
}

.row {
  display: flex;
  align-items: center;
  width: 75%;
}

input {
  width: 70px;
  margin: 0 0 0 auto;
  font-size: 24px;
  text-align: center;
  border: 2px solid var(--dark-color);
  background-color: var(--light-color);
  color: transparent;
  text-shadow: 0 0 0 var(--dark-color);
}

.bordered {
  border-color: tomato;
  background-color: rgba(255, 99, 71, 0.25);
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  opacity: 1;
}

input:focus {
  outline: none;
}

.buttons {
  min-height: 45px;
  display: flex;
}

button {
  padding: 6px 12px;
  font-size: 16px;
  letter-spacing: 2px;
  font-weight: 700;
  margin: auto;
  text-transform: uppercase;
  cursor: pointer;
  color: var(--light-color);
  background-color: var(--dark-color);
  border: none;
  border-radius: 10px;
}

button:hover {
  box-shadow: 0 0 1px 1px var(--dark-color);
}

button:active {
  transform: scale(0.95);
}

button:disabled {
  cursor: unset;
  opacity: 0.4;
  box-shadow: none;
}